(function() {
  $(function() {
    console.log("Dom Ready");
    $(".cat_del").on("click", function(event) {
      $.ajax('/forum/deleteforum', {
        type: 'DELETE',
        headers: {
          'X-CSRF-TOKEN': $('input[name=_token]').val()
        },
        data: {
          forum_id: event.target.id.split("_")[2]
        },
        error: function(jqXHR, textStatus, errorThrown) {
          return console.log(textStatus);
        },
        success: function(data, textStatus, jqXHR) {
          return $('body').append("Deleted");
        }
      });
      return console.log(event.target.id.split("_")[2]);
    });
    $('.forum_del').on("click", function(event) {
      $.ajax('/forum/deleteforum', {
        type: 'DELETE',
        headers: {
          'X-CSRF-TOKEN': $('input[name=_token]').val()
        },
        data: {
          forum_id: event.target.id.split("_")[2]
        },
        error: function(jqXHR, textStatus, errorThrown) {
          return console.log(textStatus);
        },
        success: function(data, textStatus, jqXHR) {
          return $('body').append("Deleted");
        }
      });
      return console.log(event.target.id.split("_")[2]);
    });
    return $('.change_cat').on("change", function(event) {
      $.ajax('/forum/changecat', {
        type: 'PATCH',
        headers: {
          'X-CSRF-TOKEN': $('input[name=_token]').val()
        },
        data: {
          forum_id: event.target.id.split("_")[2],
          cat_id: event.target.value
        },
        error: function(jqXHR, textStatus, errorThrown) {},
        success: function(data, textStatus, jqXHR) {}
      }, $('body').append("Deleted"));
      return console.log(event.target.value);
    });
  });

}).call(this);
